@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Style+Script&display=swap);
* {
  margin: 0px;
  padding: 0px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #2c3e50;
}
::-webkit-scrollbar {
  width: 12px;
  background-color: #2c3e50;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #4cd137;
}

.Home {
  background-color: #2c3e50;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 3% 0%;
  background-attachment: fixed;
}

/* image_Home */
.image_Home img {
  width: 137px;
  border-radius: 50%;
  height: 152px;
  box-shadow: rgb(0 0 0 / 32%) 0px 2px 10px 0px;
}

.svg_home {
  position: absolute;
  top: 0px;
  left: 0px;
}

/* Data_Home */
.Data_Home {
  margin: 5px 0px;
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.text_Home {
  color: white;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
}
.para_Home {
  margin-top: 9px;
  color: lightgray;
  text-indent: 87px;
  font-size: 17px;
  letter-spacing: 2px;
  font-family: "Roboto";
}

/* social icon */

.socialLink_Home {
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.socialLink_Home a {
  margin-bottom: -4px;
}
.icon {
  font-size: 26px;
  color: white;
  margin: 0px 5px;
}
.button_Home {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 6px;
}
.Home_Cv_Button {
  text-decoration: none;
}

@media only screen and (max-width: 550px) {
  .Data_Home {
    width: 85%;
  }
}

.MobileNavBar {
  display: none;
}

@media only screen and (max-width: 550px) {
  .MobileNavBar {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #2c3e50;
    padding: 5px 7px;
    position: sticky;
    top: 0px;
    z-index: 999;
  }

  /* Data_NavBar */
  .Mobile_Data_NavBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  /* Logo_Nav  */
  .Mobile_Logo_Nav img {
    width: 80px;
    height: 80px;
  }

  /* Links_Nav */
  .Mobile_Links_Nav {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 65px;
    right: 20px;
    background-color: #44bd32;
    z-index: 1000;
    padding: 13px 35px;
    border-radius: 15px;
  }
  .Mobile_link_Nav {
    text-decoration: none;
    color: white;
    font-size: 20px;
    margin: 7px 0px;
    font-family: system-ui;
  }

  .progress-container {
    width: 100%;
    height: 4px;
    background: #2c3e50;
    position: absolute;
    bottom: 0px;
  }

  /* The progress bar (scroll indicator) */
  .progress-bar {
    height: 4px;
    background: #44bd32;
    width: 0%;
  }

  .Mobile_menu {
    color: white;
    font-size: 35px;
    cursor: pointer;
  }
}

.NavBar {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #2c3e50;
  padding: 5px 7px;
  position: sticky;
  top: 0px;
  z-index: 999;
}

/* Data_NavBar */
.Data_NavBar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

/* Logo_Nav  */
.Logo_Nav img {
  width: 80px;
  height: 80px;
}

/* Links_Nav */
.Links_Nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
.link_Nav {
  text-decoration: none;
  color: white;
  font-size: 20px;
  margin: 0px 7px;
  font-family: system-ui;
}

/* The progress container (grey background) */
.progress-container {
  width: 100%;
  height: 4px;
  background: #2c3e50;
  position: absolute;
  bottom: 0px;
}

/* The progress bar (scroll indicator) */
.progress-bar {
  height: 4px;
  background: #44bd32;
  width: 0%;
}

@media only screen and (max-width: 800px) {
  .Data_NavBar {
    justify-content: space-between;
  }
}
@media only screen and (max-width: 550px) {
  .NavBar {
    display: none;
  }
}

.Projects {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #ecf0f1;
  padding: 5px 0px;
}
.Projects h1 {
  font-size: 30px;
  font-family: "Roboto";
  letter-spacing: 2px;
  font-weight: bold;
  border-bottom: 1px solid #2c3e50;
  color: #2c3e50;
}

.card_project {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 0px;
}
.card_project_data {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px,
    rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
  background-color: #f7f5eb;
  width: 800px;
}
.arrow_project {
  font-size: 30px;
  margin: 0px 10px;
  font-weight: bold;
  color: #273c75;
  cursor: pointer;
}
.card_project_data img {
  object-fit: cover;
  width: 50%;
  height: 240px;
}
.card_project_data h3 {
  font-size: 23px;
  margin: 5px 0px;
  font-family: "Roboto";
  letter-spacing: 2px;
}
.card_project_data span {
  font-family: "Roboto";
  text-indent: 20px;
  margin: 20px 4px;
}
.card_project_data a {
  background-color: #4cd137;
  padding: 4px 20px;
  outline: none;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  font-size: 20px;
  width: 140px;
  text-align: center;
}
.card_project_data a:hover {
  background-color: #44bd32;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.card_porject_details {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 50%;
}

@media only screen and (max-width: 900px) {
  .card_project_data {
    width: 90%;
    flex-direction: column;
  }
  .card_project_data img {
    object-fit: cover;
    width: 100%;
  }
  .card_porject_details {
    width: 90%;
    padding-bottom: 5px;
  }
  .card_project_data span {
    margin: 9px 4px;
  }
  .card_project_data h3 {
    margin-top: 10px;
  }
}

@media only screen and (max-width: 550px) {
  .arrow_right {
    position: absolute;
    right: 3%;
    color: white;
    background-color: rgba(25, 42, 86, 0.8);
    border-radius: 50%;
    padding: 3px;
  }
  .arrow_left {
    position: absolute;
    left: 3%;
    color: white;
    background-color: rgba(25, 42, 86, 0.8);
    border-radius: 50%;
    padding: 3px;
  }
}

.Quote {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #4cd137;
  padding: 10px 0px;
}
.Quote p {
  font-size: 34px;
  color: white;
  font-family: "Style Script", cursive;
  letter-spacing: 2px;
}
.Quote_Icon {
  font-size: 30px;
  margin: 0px 13px;
  color: white;
}
.wave_Quote img {
  width: 100%;
  margin-top: -14px;
}

@media only screen and (max-width: 550px) {
  .Quote_Icon {
    font-size: 15px;
  }
  .Quote p {
    font-size: 22px;
  }
}

.skill {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: -7%;
  margin-bottom: 10px;
}

/* skill header */
.skill h1 {
  font-size: 30px;
  font-family: "Roboto";
  letter-spacing: 2px;
  font-weight: bold;
  border-bottom: 1px solid #2c3e50;
  color: #2c3e50;
}

.data_skill {
  width: 60%;
  margin: 15px 0px;
}

.allData {
  margin: 5px 0px;
}

.skill_name {
  font-size: 18px;
  font-family: monospace;
  color: #2c3e50;
}
.progressBar {
  width: 100%;
  background-color: #9fa9bc;
  border-radius: 10px;
}
.progress {
  border-radius: 10px;
  background-color: #5c6577;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.progress span {
  margin-right: 5px;
}

@media only screen and (max-width: 550px) {
  .data_skill {
    width: 80%;
  }
}


